<template>
  <div>
    <el-container>
      <el-header style="text-align: right"></el-header>
      <el-container>
        <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
          <el-menu :default-openeds="['1']" router="true">
            <el-submenu index="1">
              <template slot="title"
                ><i class="el-icon-message"></i>菜单</template
              >
              <el-menu-item-group>
                <el-menu-item index="/student_homepage_personal_info"
                  >个人信息</el-menu-item
                >
                <el-menu-item index="/student_course_choose_to_learn"
                  >课程学习</el-menu-item
                >
                <el-menu-item index="/student_course_select"
                  >选择课程</el-menu-item
                >
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-aside>

        <el-main>
          <el-table :data="courses" style="width: 100%">
            <el-table-column prop="courseID" label="课程ID" width="180">
            </el-table-column>
            <el-table-column prop="courseName" label="课程名" width="180">
            </el-table-column>
            <el-table-column prop="description" label="课程说明">
            </el-table-column>
            <el-table-column label="操作" width="180">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  size="mini"
                  @click="startLearning(scope.row)"
                  >开始学习</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
      
      <script>
import axios from "axios";

export default {
  data() {
    return {
      courses: [],
    };
  },
  methods: {
    startLearning(row) {
      // 跳转至学习界面
      this.$router.push({ path: "/course_learning_page", query: row });
    },
  },
  mounted() {
    let token = localStorage.getItem("token");
    axios
      .get(
        "http://localhost:8080/SongGuo_Learning/student/selectParticipatedCourses?studentID=" +
          localStorage.getItem("id"),
        {
          headers: {
            Token: token,
          },
        }
      )
      .then((resp) => {
        if (resp.data.message == "TOKEN_IS_NULL") {
          this.$router.push("/studentlogin");
          return;
        }
        this.courses = resp.data.data;
      });
  },
};
</script>
      
      <style>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  line-height: 160px;
}
</style>